@import "../../../stylesheets/core/_variables.scss";
@import "../../../stylesheets/core/_mixins.scss";

.character__detail {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &--navigation {
    display: flex;
    justify-content: center;
    margin-bottom: 120px;

    li {
      padding: 0 20px;
    }
    & .navigation--left {
      width: 300px;
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }

    & .navigation--right {
      display: flex;
      width: 300px;
      align-items: center;
      justify-content: flex-start;
    }

    & .image--preview {
      display: flex;
      justify-content: space-between;
      align-items: center;

      & img {
        width: 45px;
        height: 45px;
        border-radius: 100%;
        border: 1px solid $color-light;
        margin: 10px;
        order: 2;
      }
    }

    & .text--preview {
      color: $color-light;
      font-size: $font-small;
      order: 3;
    }
    & .order-01 {
      order: 1;
    }
  }
}
